'use client'

import * as React from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import { Home, FileText, Settings, Menu, ChevronLeft } from "lucide-react"
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet"
import { Button } from "@/components/ui/button"

const navItems = [
  { name: "Dashboard", href: "/dashboard", icon: Home },
  { name: "Machine", href: "/devices", icon: Settings },
  { name: "Receipts", href: "/receipts", icon: FileText },
  { name: "Settings", href: "/settings", icon: Settings },
]

// -------------------- Desktop Sidebar --------------------
export function Sidebar() {
  const pathname = usePathname()
  const [collapsed, setCollapsed] = React.useState(false)

  const NavLinks = () => (
    <nav className="flex flex-col space-y-2 mt-6 px-3">
      {navItems.map((item) => {
        const Icon = item.icon
        const active = pathname.startsWith(item.href)
        return (
          <Link
            key={item.href}
            href={item.href}
            className={cn(
              "flex items-center gap-3 rounded-lg py-2 px-2 text-sm font-medium transition",
              active
                ? "bg-[#1C5780] text-white"
                : "text-blue-100 hover:bg-[#1C5780] hover:text-white"
            )}
          >
            <Icon className="h-5 w-5" />
            {!collapsed && <span>{item.name}</span>}
          </Link>
        )
      })}
    </nav>
  )

  return (
    <aside
      className={cn(
        "hidden md:flex flex-col border-r border-[#164A73] bg-[#1F628E] text-white transition-all duration-300 h-screen fixed top-0 left-0 p-4",
        collapsed ? "w-20" : "w-64"
      )}
    >
      {/* Logo + Title */}
      <div className="flex items-center justify-between mb-10">
        <div className="flex items-center gap-3">
          <img
            src="/BGI-logo1.png"
            alt="BGI Logo1"
            className="h-16 w-auto object-contain" // increased logo size
          />
          {!collapsed && (
            <span className="text-lg font-bold text-white">E-Voucher</span>
          )}
        </div>

        <Button
          variant="ghost"
          size="icon"
          className="text-white hover:bg-[#1C5780]"
          onClick={() => setCollapsed(!collapsed)}
        >
          <ChevronLeft
            className={cn(
              "h-5 w-5 transition-transform",
              collapsed && "rotate-180"
            )}
          />
        </Button>
      </div>

      <div className="flex-1">
        <NavLinks />
      </div>
    </aside>
  )
}

// -------------------- Mobile Sidebar Button + Drawer --------------------
export function MobileSidebarButton() {
  const [open, setOpen] = React.useState(false)
  const pathname = usePathname()

  return (
    <Sheet open={open} onOpenChange={setOpen}>
      <SheetTrigger asChild>
        <Button
          variant="default"
          size="icon"
          className="md:hidden fixed top-4 right-4 z-50 rounded-full shadow-lg bg-[#1F628E] text-white"
        >
          <Menu className="h-6 w-6" />
        </Button>
      </SheetTrigger>

      <SheetContent
        side="left"
        className="w-64 p-6 bg-[#1F628E] text-white border-none"
      >
        {/* Logo + Title */}
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-3">
            <img
              src="/BGI-logo1.png"
              alt="BGI Logo"
              className="h-12 w-auto object-contain" // increased logo size
            />
            <span className="text-lg font-bold">E-Voucher</span>
          </div>

          <Button
            variant="ghost"
            size="icon"
            className="text-white hover:bg-[#1C5780]"
            onClick={() => setOpen(false)}
          >
            <ChevronLeft className="h-5 w-5" />
          </Button>
        </div>

        <nav className="flex flex-col space-y-3 px-2">
          {navItems.map((item) => {
            const active = pathname.startsWith(item.href)
            return (
              <Link
                key={item.href}
                href={item.href}
                onClick={() => setOpen(false)}
                className={cn(
                  "px-3 py-2 rounded-md text-sm font-medium transition",
                  active
                    ? "bg-[#1C5780] text-white"
                    : "text-blue-100 hover:bg-[#1C5780] hover:text-white"
                )}
              >
                {item.name}
              </Link>
            )
          })}
        </nav>
      </SheetContent>
    </Sheet>
  )
}
